.modal {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.6);
    font-size: 40px;
}
.modal button {
    outline: none;
    cursor: pointer;
    border: 0;
}
.modal > section {
    width: 90%;
    width: 700px;
    margin:0 auto;
    border-radius: .3rem;
    background-color: #fff;
    /* 팝업이 열릴때 스르륵 열리는 효과 */
    -webkit-animation: modal-show .3s;
            animation: modal-show .3s;
    overflow: hidden;
}
.modal > section > header {
    position: relative;
    padding: 16px 64px 16px 16px;
    background-color: #f1f1f1;
    font-weight: 700;
}
.modal > section > header button {
    position: absolute;
    top: 15px;
    right: 15px;
    width: 40px;
    height: 50px;
    font-size: 40px;
    font-weight: 1000;
    text-align: center;
    color: #999;
    background-color: transparent;
}
.modal > section > main {
    padding: 16px;
    border-bottom: 1px solid #dee2e6;
    border-top: 1px solid #dee2e6;
    font-size: 25px;
    height: 500px;
}
.modal > section > footer {
    padding: 12px 16px;
    text-align: right;
    font-size: 20px;
}
.modal > section > footer button {
    padding: 6px 12px;
    margin-left: 15px;
    margin-bottom: 5px;
    color: #fff;
    background-color: #6c757d;
    border-radius: 5px;
    font-size: 20px;
    width: 200px;
